* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Montserrat', 'Arial', sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 1.2;
  color: #000;
}

/* Global classes */

img {
  max-width: 100%;
  height: auto;
}

.wrapper {
  max-width: 1200px;
  margin: 0 auto;
  padding-left: 30px;
  padding-right: 30px;
}

.visually-hidden {
  width: 1px;
  height: 1px;
  overflow: hidden;
  margin-top: -1px;
  margin-left: -1px;
  position: absolute;
  clip: rect(0, 0, 0, 0);
  border: none;
}

.btn {
  background-color: #5b815d;
  width: 213px;
  height: 57px;
  line-height: 57px;
  display: inline-block;
  text-align: center;
  color: #fffefe;
  font-weight: 600;
  border: none;
  text-decoration: none;
  border-radius: 29px;
  box-shadow: 0px 7px 21px 0px rgba(55, 125, 60, 0.3);
}

/* Atomic classes */

.d-n {
  display: none;
}

.d-b {
  display: block;
}

/* General */

.header-navigation {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
}

.header {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  padding-top: 48px;
  min-height: 100px;
}

.header .menu {
  flex-grow: 1;
  display: flex;
  justify-content: flex-end;
  list-style: none;
}

.header .navigation__a {
  position: relative;
  text-decoration: none;
  color: black;
  margin: 0 0 6px 20px;
  font-size: 14px;
  font-weight: 600;
}

.header .navigation__a:after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  width: 0;
  border-bottom: 1px solid #97c89a;
  transition: width 0.5s;
}

.header .navigation__a:hover:after {
  width: 100%;
}

.header .navigation__a:nth-child(1) {
  padding: 0;
}

.header .navigation__a:nth-child(5) {
  margin: 0 0 0 53px;
}

.main {
  border-top: 1px;
}

.first-screen {
  padding-top: 185px;
  padding-bottom: 266px;
  background: url(../img/Fon.jpg) no-repeat;
  background-size: cover;
  min-height: 100vh;
}

.slogan__text {
  font-size: 90px;
  width: calc(58.333333333333336% - 20px);
  margin-bottom: 55px;
}

.slogan__btn {
  transition: background-color 0.3s;
}

.slogan__btn:hover {
  background-color: #5b815d;
  color: #fff;
}

.slogan__btn_white {
  margin-left: 25px;
  border: 2px solid #5b815d;
  background-color: transparent;
  color: #000;
  box-shadow: unset;
}

.slogan__btn_white:hover {
  background-color: #5b815d;
  border: none;
}

.gradient {
  padding-top: 184px;
  background: linear-gradient(to top right, #267ebe, #6dd5ed);
}

.feedback_colortext {
  color: #ffffff;
  margin-bottom: 80px;
  font-size: 60px;
}

.my-row {
  display: flex;
  flex-wrap: wrap;
  margin-left: -10px;
  margin-right: -10px;
}

.feedback-card {
  background-color: #ffffff;
  border-radius: 10px;
  margin-bottom: 45px;
  padding-top: 50px;
  padding: 0 33px;
  padding-bottom: 75px;
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 10px;
  margin-right: 10px;
  width: calc(33.333333333333336% - 20px);
}

.feedback-card__face {
  margin: 68px 0;
}

.feedback-card__text {
  margin: 24px 40px 0 0;
  font-weight: 500;
  margin-bottom: 36px;
}

.screen {
  height: 100vh;
}

.features {
  text-align: center;
  margin-bottom: 36px;
}

.centerP {
  text-align: center;
  width: 50%;
  margin: 0 auto;
  margin-bottom: 46px;
}

.advantages {
  display: flex;
  flex-wrap: wrap;
  margin-left: -10px;
  margin-right: -10px;
}

.advantages-li {
  list-style-type: none;
  padding-top: 115px;
  text-align: center;
  padding-bottom: 14px;
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 10px;
  margin-right: 10px;
  width: calc(25% - 20px);
  position: relative;
}

.advantages-li + li {
  border-left: 1px solid #dddddd;
}

.advantages-li:before {
  content: '';
  background-image: url(../img/noun_Coffee.png);
  background-color: #eaf4eb;
  background-repeat: no-repeat;
  background-position: center;
  width: 55px;
  height: 55px;
  position: absolute;
  top: 40%;
  left: 50%;
  margin-top: -27.5px;
  margin-left: -27.5px;
  border-radius: 50%;
}

.advantages-li__Tasty:before {
  background-image: url(../img/noun_muffin.png);
}

.advantages-li__WiFi:before {
  background-image: url(../img/noun_wifi.png);
}

.advantages-li__Comfortable:before {
  background-image: url(../img/noun_Sofa.png);
}

@media screen and (max-width: 960px) {
  .feedback-card {
    width: calc(50% - 20px);
  }

  .advantages-li {
    width: calc(50% - 20px);
  }
}

@media screen and (max-width: 780px) {
  .wrapper {
    padding-left: 15px;
    padding-right: 15px;
  }

  .feedback-card {
    width: calc(50% - 20px);
  }

  .advantages-li {
    width: calc(50% - 20px);
  }
}

@media screen and (max-width: 560px) {
  .feedback-card {
    width: calc(100% - 20px);
  }

  .advantages-li {
    width: calc(100% - 20px);
  }
}